.cart {
  position: fixed;
  top: 0px;
  right: 0px;
  width: 80%;
  height: 95%;
  background-color: rgb(27, 26, 32);
  box-sizing: border-box;
  z-index: 99;
  transition: right 0.2s ease 0s;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 45px 0;
}

.heading {
  font-size: 1.25rem;
}

.item-list {
  list-style: none;
  padding: 0;
  margin: 0;
  overflow-y: scroll;
  width: 100%;
}


.total-details {
  background-color: whitesmoke;
  color: black;
  width: 40%;
  align-self: flex-end;
  text-align: end;
  padding: 0px 10px;
}

.price,
.quantity {
  font-family: bold;
}

.cartIsOpen {
  animation: openCart 0.4s ease-out forwards;
}

.cartIsClosed {
  animation: closeCart 0.4s ease-out forwards;
}


@keyframes openCart {
  0% {
    opacity: 0;
    transform: translateX(100%);
  }

  100% {
    opacity: 1;
    transform: translateX(0%);
  }
}



@media (min-width: 600px) {

  .cart {
    width: 50%;
  }
}

@media (min-width: 1024px) {
  .cart {
    width: 30%;
    height: 100%;
    padding: 45px 0;
  }

}

@media (min-width: 1240px) {}


@keyframes closeCart {
  0% {
    opacity: 1;
    transform: translateX(0%);
  }

  100% {
    opacity: 0;
    transform: translateX(100%);
  }
}