.form {
  background-color: whitesmoke;
  max-width: 440px;
  margin: auto;
  margin-top: 50px;
  margin-bottom: 50px;
  padding: 45px;
  text-align: center;
  border-radius: 10px;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2), 0 3px 3px 0 rgba(0, 0, 0, 0.24);
  overflow: hidden;
  background-color: whitesmoke;
  font-family: 'Alkalami', serif;
}

.heading{
  margin-bottom: 50px;
  font-size: 30px;
  color: black;
}

.input{
  border-radius: 10px;
  width: 100%;
  height: 50px;
  border: 0;
  margin: 0 0 15px;
  padding: 15px;
  box-sizing: border-box;
  font-size: 14px;
  font-family: 'Alkalami', serif;
  background-color: white;
}

.form textarea {
  background: #f8f4b4;
  border-radius: 10px;
  border: 1px solid rgb(206, 206, 206);
  width: 99%;
  max-width: 99%;
  min-width: 99%;
  min-height: 50px;
  margin-bottom: 10px;

}

.btn {
  font-family: 'Alkalami', serif;
  text-transform: uppercase;
  width: 100%;
  border: 0;
  padding: 15px;
  color: white;
  border-radius: 10px;
  margin-top: 10px;
  display: block;
  font-size: 21px;
  text-align: center;
  background: rgb(214, 112, 16);
}

.btn:hover,
.btn:active,
.btn:focus {
  cursor: pointer;
  color:  rgb(214, 112, 16);
  background-color: white;
}

.error {
  color: red;
}